<template>
  <div class="container-fluid d-flex align-items-center justify-content-center">
    <div class="cardSignUp container">
      <div class="lenta">
        <span class="span1"></span><span class="span2"></span><span class="span3"></span>
        <h1 class="mt-4">ACCAUNT OCHISH UCHUN RO'YHATDAN O'TING</h1>
      </div>
      <form class="p-4" style="text-align: left">
        <div class="mb-3">
          <label for="name" class="form-label">Foydalanuvchi nomingiz</label>
          <input
            v-model="userName"
            type="text"
            :class="{ 'is-invalid': simvolCounter != 0 }"
            class="form-control"
            id="name"
            placeholder="Foydalanuvchi nomingiz"
          />
        </div>
        <div v-if="simvolCounter != 0" class="alert alert-danger" role="alert">
          Mumkun bo'lmagan belgilar kiritildi
        </div>

        <div class="mb-3 position-relative">
          <label for="password" class="form-label">Parol yarating</label>
          <div class="position-relative">
            <input
              v-model="userPassword"
              ref="inputP"
              type="password"
              :class="{
                'border-danger': passwordError,
                'border-success': regCpmpleateSee,
              }"
              class="form-control"
              id="password"
            />
            <i
              :class="{ 'fas fa-eye': !passwordSee, 'fas fa-eye-slash': passwordSee }"
              @click="see1"
            ></i>
          </div>
        </div>
        <div v-if="passwordError" class="alert alert-danger" role="alert">
          Parol eng kami 6 belgili bo'lishi shart
        </div>
        <div class="mb-3 position-relative">
          <label for="password_return" class="form-label">Parolni takrorlang</label>
          <div class="position-relative">
            <input
              :disabled="passwordError"
              v-model="passRet"
              ref="inputPR"
              type="password"
              :class="{
                'border-danger': passwordRetError,
                'border-success': regCpmpleateSee,
              }"
              class="form-control"
              id="password_return"
            />
            <i
              :class="{ 'fas fa-eye': !passwordSee2, 'fas fa-eye-slash': passwordSee2 }"
              @click="see2"
            ></i>
          </div>
        </div>
        <div v-if="passwordRetError" class="alert alert-danger" role="alert">
          Parolni qayta kiritishda xatolik
        </div>

        <router-link @click="regCpmpleateSend" to="/chat" class="btn btn-success"
          >Ro'yhatdan o'tish</router-link
        >
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Test",
  created() {},
  data() {
    return {
      passwordSee: true,
      passwordSee2: true,
      userName: "",
      userPassword: "",
      passRet: "",
      passwordError: false,
      passwordRetError: false,
      regCpmpleate: false,
      regCpmpleateSee: false,
      simvollar: [
        "!",
        "@",
        "#",
        "$",
        "%",
        "^",
        "&",
        "*",
        "(",
        ")",
        "-",
        "=",
        "+",
        "|",
        "1",
        "2",
        "3",
        "4",
        "5",
        "6",
        "7",
        "8",
        "9",
        "_",
        "''",
      ],
      simvolCounter: 0,
    };
  },
  props: {},
  methods: {
    see1() {
      this.passwordSee = !this.passwordSee;
      if (this.passwordSee) this.$refs.inputP.type = "password";
      else this.$refs.inputP.type = "text";
    },
    see2() {
      this.passwordSee2 = !this.passwordSee2;
      if (this.passwordSee2) this.$refs.inputPR.type = "password";
      else this.$refs.inputPR.type = "text";
    },
    regCpmpleateSend() {
      if (this.userPassword == this.passRet) {
        localStorage.removeItem("logIn", true);
        localStorage.setItem("logIn", true);
      }
    },
  },
  watch: {
    userName(e) {
      this.simvolCounter = 0;
      this.simvollar.filter((arry) => {
        if (e.includes(arry)) this.simvolCounter++;
      });
    },
    userPassword(e) {
      if (e.length < 6) this.passwordError = true;
      else this.passwordError = false;
    },
    passRet(e) {
      if (e == this.userPassword) {
        this.passwordRetError = false;
        this.regCpmpleate = false;
        this.regCpmpleateSee = true;
      } else {
        this.regCpmpleateSee = false;
        this.passwordRetError = true;
        this.regCpmpleate = true;
      }
    },
  },
};
</script>

